
import {defineComponent} from "vue";

let timer: any = null;

export default defineComponent({
  data() {
    return {
      step:  1
    }
  },
  methods: {
    getImgUrl(pic: string) {
      return require('./assets/'+pic)
    },
    next(): void {
      if (timer !== null) {
        clearInterval(timer)
      }
      if (this.step === 9) {
        alert("test 완료 설문조사를 해주세요.");
        return;
      }
      this.step = this.step + 1;
      if (this.step === 4 || this.step === 6) {
        timer = setTimeout(() => {
          this.next();
        }, 1000)
      }
    },
    checkActive(index: number) {
      let step = this.step;
      if (step == 4) {
        step = 3;
      } else if (step == 5 || step == 6) {
        step = 4;
      } else if (step >= 7) {
        step = this.step -2
      }
      if (index === step) {
        return true;
      }
      return false;
    }
  }
})
